<template>
  <div class="project-management full-screen">
    <div class="page-panel" :class="{white: (currentIndex === 0 && !collectionList.length) || (currentIndex === 1 && !deliveryList.length)}">
      <div class="project-management-tabs">
        <div class="project-management-tab" v-for="(item, index) in tabs" :key="index" :class="{active: currentIndex === index}" @click="switchItem(index)">{{ item }}<span v-if="managerFlag && index === 1" class="unread"></span></div>
      </div>
      <div class="project-management-contents">
        <pem-scroll-view
          ref="collectionList"
          class="scroll-view"
          v-if="!isCollectionLoading && currentIndex === 0 && !!collectionList.length"
          :data="collectionList"
          :pullup=true
          :pulldown=true
          @pullUpLoad="collectionListMore"
          @pullDownRefresh="collectionListRefresh">
          <div class="scroll-view-container">
            <div class="scroll-view-pulldown">下拉刷新...</div>
            <pem-project-list :data="collectionList" :showDesc=false :showMask=false :canRead=false @alert="alert"/>
            <div class="scroll-view-pullup">{{ isCollectionListMore ? '加载中...' : '目前就这么多了' }}</div>
          </div>
        </pem-scroll-view>
        <div v-if="!isCollectionLoading && currentIndex === 0 && !collectionList.length" class="empty-result">
          <div class="empty-result-icon icon-collection"></div>
          <span>暂无已收藏的项目</span>
        </div>
        <pem-scroll-view
          ref="deliveryList"
          class="scroll-view"
          v-if="!isDeliveryLoading && currentIndex === 1 && !!deliveryList.length"
          :data="deliveryList"
          :pullup=true
          :pulldown=true
          @pullUpLoad="deliveryListMore"
          @pullDownRefresh="deliveryListRefresh">
          <div class="scroll-view-container">
            <div class="scroll-view-pulldown">下拉刷新...</div>
            <pem-project-list :data="deliveryList" :showDesc=false :showMask=false :canRead=false showUnread @alert="alert"/>
            <div class="scroll-view-pullup">{{ isDeliveryListMore ? '加载中...' : '目前就这么多了' }}</div>
          </div>
        </pem-scroll-view>
        <div v-if="!isDeliveryLoading && currentIndex === 1 && !deliveryList.length" class="empty-result">
          <div class="empty-result-icon icon-delivery"></div>
          <span>暂无投递给我的项目</span>
        </div>
      </div>
    </div>
    <pem-operation-panel @backtotop="backtotop" />
    <pem-loading v-show="isLoading"></pem-loading>
    <pem-toast ref="toast" :text="toastText"></pem-toast>
    <pem-dialog ref='alert' :text="alertText" :confirmBtnText="alertBtnText" :type="alertType" @confirm="alertCallback"></pem-dialog>
  </div>
</template>

<script type="text/ecmascript-6">
import PemOperationPanel from 'components/pem-operation-panel/pem-operation-panel'
import PemScrollView from 'components/scroll-view/scroll-view'
import PemProjectList from 'components/pem-project-list/pem-project-list'
import PemToast from 'components/pem-toast/pem-toast'
import PemDialog from 'components/dialog/dialog'
import PemLoading from 'components/pem-loading/pem-loading'
import { mapState } from 'vuex'
import { MiniSiteApi, ImgPath } from 'api'
import { setTitle } from 'common/js/util'

export default {
  components: {
    PemOperationPanel,
    PemScrollView,
    PemProjectList,
    PemToast,
    PemDialog,
    PemLoading
  },
  data () {
    return {
      currentIndex: 0,
      tabs: ['收藏项目', '投递给我'],
      collectionList: [],
      deliveryList: [],
      currentCollectionPage: 1,
      currentDeliveryPage: 1,
      pageSize: 10,
      toastText: '',
      isCollectionLoading: true,
      isDeliveryLoading: true,
      managerFlag: false,
      alertText: '',
      alertType: '',
      alertBtnText: '',
      alertBtnCallback: null,
      isLoading: true,
      isCollectionListMore: true,
      isDeliveryListMore: true
    }
  },
  created () {
    this._initWXShare()
    this._setTitle('项目工场-项目管理')
    setTimeout(() => {
      this._initData()
    }, 20)
  },
  computed: mapState({auth: state => state.auth}),
  methods: {
    _initWXShare () {
      this.wxShare({
        title: '为您推荐项目工场',
        content: '我正在使用项目工场，有料的BP、有趣的VC都在这儿，一起来看项目吧！',
        linkUrl: window.location.origin
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    _initData () {
      this.managerFlag = this.auth.managerFlag
      this.getList('collection', 'init')
    },
    backtotop () {
      if (this.$refs.collectionList) this.$refs.collectionList.scrollTo(0, 0, 300)
      if (this.$refs.deliveryList) this.$refs.deliveryList.scrollTo(0, 0, 300)
    },
    switchItem (index) {
      if (this.currentIndex === index) {
        return false
      }
      this.currentIndex = index
      this.getList(['collection', 'delivery'][index], 'init')
    },
    toast (text) {
      this.toastText = text
      this.$refs.toast.show()
    },
    alert (text, options = {}) {
      this.alertText = text
      this.alertType = options.type || 'alert'
      this.alertBtnText = options.btn || '确定'
      this.alertBtnCallback = options.callback || null
      this.$refs.alert.show()
    },
    alertCallback () {
      this.alertBtnCallback && this.alertBtnCallback()
    },
    collectionListMore () {
      if (!this.isLoading && this.isCollectionListMore) {
        this.getList('collection', 'more')
      }
    },
    collectionListRefresh () {
      if (!this.isLoading) {
        this.isCollectionListMore = true
        this.getList('collection', 'init')
      }
    },
    deliveryListMore () {
      if (!this.isLoading && this.isDeliveryListMore) {
        this.getList('delivery', 'more')
      }
    },
    deliveryListRefresh () {
      if (!this.isLoading) {
        this.isDeliveryListMore = true
        this.getList('delivery', 'init')
      }
    },
    getList (itemType, type = 'init') {
      this.isLoading = true
      if (itemType === 'collection') {
        if (type === 'init') {
          this.currentCollectionPage = 1
        }
        this._getFavoriteList({
          currentPage: this.currentCollectionPage,
          pageSize: this.pageSize
        }, data => {
          this.isLoading = false
          this.isCollectionLoading = false
          switch (+data.resultcode) {
            case 1:
              this.currentCollectionPage = (+data.currentPage || 1) + 1
              if (+data.totalPage <= +data.currentPage) {
                this.isCollectionListMore = false
              }
              let _collectionList = (type === 'init') ? [] : this.collectionList
              _collectionList = _collectionList.concat(data.resultList || []).map(item => {
                item.logo = item.logo ? (ImgPath + item.logo.replace(ImgPath, '')) : null
                if (item.iconList) {
                  item.iconList = item.iconList.map(icon => {
                    icon.imgurl = icon.imgurl ? (ImgPath + icon.imgurl.replace(ImgPath, '')) : null
                    return icon
                  })
                }
                return item
              })
              this.collectionList = _collectionList
              break
            case -10:
              this.toast('系统异常')
              break
            case -11:
              this.toast('用户登录异常')
              break
            case -12:
              this.toast('页码信息异常')
              break
            default:
              this.toast('未知错误，请联系管理员')
          }
        })
      }
      if (itemType === 'delivery') {
        if (type === 'init') {
          this.currentDeliveryPage = 1
        }
        this._myDeliverList({
          currentPage: this.currentDeliveryPage,
          pageSize: this.pageSize
        }, data => {
          this.isLoading = false
          this.isDeliveryLoading = false
          switch (+data.resultcode) {
            case 1:
              this.currentDeliveryPage = (+data.currentPage || 1) + 1
              if (+data.totalPage <= +data.currentPage) {
                this.isDeliveryListMore = false
              }
              let _deliveryList = (type === 'init') ? [] : this.deliveryList
              _deliveryList = _deliveryList.concat(data.resultList || []).map(item => {
                item.logo = item.logo ? (ImgPath + item.logo.replace(ImgPath, '')) : null
                if (item.iconList) {
                  item.iconList = item.iconList.map(icon => {
                    icon.imgurl = icon.imgurl ? (ImgPath + icon.imgurl.replace(ImgPath, '')) : null
                    return icon
                  })
                }
                return item
              })
              this.deliveryList = _deliveryList
              break
            case -10:
              this.toast('用户登录异常')
              break
            case -11:
              this.toast('页码信息异常')
              break
            default:
              this.toast('未知错误，请联系管理员')
          }
        })
      }
    },
    _getFavoriteList (options, callback) {
      MiniSiteApi.getFavoriteList(options).then(data => {
        callback(data)
      })
    },
    _myDeliverList (options, callback) {
      MiniSiteApi.myDeliverList(options).then(data => {
        callback(data)
      })
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .project-management
    font-size: 0.24rem
    .project-management-tabs
      padding: 0.14rem 0
      background-color: $color-panel
      font-size: 0
      line-bottom()
      .project-management-tab
        font-size: 0.28rem
        color: $color-text-disable
        line-height: 0.6rem
        text-align: center
        display: inline-block
        width: 3.75rem
        font-weight: bold
        &.active
          color: $color-text-title
          position: relative
          &:after
            position: absolute
            content: ''
            width: 0.42rem
            height: 0.04rem
            background-color: #FFD301
            left: 50%
            margin-left: -0.21rem
            bottom: -0.12rem
        .unread
          width: 6px
          height: 6px
          border-radius: 6px
          background-color: #eb4b36
          display: inline-block
          vertical-align: text-top
    .project-management-contents
      position: absolute
      bottom: 0
      left: 0
      right: 0
      top: 1rem
    .empty-result
      font-size: 0
      text-align: center
      padding-top: 0.6rem
      .empty-result-icon
        display: inline-block
        width: 3.56rem
        height: 3.56rem
        background-size: 3.56rem 3.56rem
        &.icon-collection
          bg-image('icon-Collection-Empty state')
        &.icon-delivery
          bg-image('icon-Delivery-Empty state')
      span
        display: block
        font-size: 0.32rem
        color: $color-text-title
        line-height: 0.44rem
</style>



